const config = {
  siteTitle: "ezBlog", // Site title.
  siteTitleShort: "ez", // Short site title for homescreen (PWA). Preferably should be under 12 characters to prevent truncation.
  siteTitleAlt: "ez Site", // Alternative site title for SEO.
  siteLogo: "/logos/logo.png", // Logo used for SEO and manifest.
  siteUrl: "https://www.ezfire.me", // Domain of your website without pathPrefix.
  pathPrefix: "", // Prefixes all links. For cases when deployed to example.github.io/gatsby-advanced-starter/.
  siteDescription: "My current personal website. Serves as a portfolio, Blog, and introduction to who I am.", // Website description used for RSS feeds/meta description tag.
  siteRss: "/rss.xml", // Path to the RSS file.
  googleAnalyticsID: "UA-47311644-5", // GA tracking ID.
  disqusShortname: "gatsby-advanced-starter", // Disqus shortname.
  postDefaultCategoryID: "Thoughts", // Default category for posts.
  dateFromFormat: "YYYY-MM-DD", // Date format used in the frontmatter.
  dateFormat: "DD/MM/YYYY", // Date format for display.
  userName: "ezfire", // Username to display in the author segment.
  userEmail: "ezamudio@hawk.iit.edu", // Email used for RSS feed's author segment
  userTwitter: "ed_13", // Optionally renders "Follow Me" in the UserInfo segment.
  userLocation: "Chicago,IL", // User location to display in the author segment.
  userAvatar: "https://api.adorable.io/avatars/150/test.png", // User avatar to display in the author segment.
  userDescription:
    "Student of Life", // User description to display in the author segment.
  menuLinks: [
    {
      name: 'Home',
      link: '/',
    },
    {
      name: 'About',
      link: '/about/',
    },
    {
      name: 'Work',
      link: '/work/',
    },
    {
      name: 'Blog',
      link: '/journal/',
    }
  ],
  // Links to social profiles/projects you want to display in the author segment/navigation bar.
  userLinks: [
    {
      label: "Mode",
      url: "",
      iconClassName: "fa fa-github"
    },
    {
      label: "GitHub",
      url: "https://github.com/notedwin",
      iconClassName: "fa fa-github"
    },
    {
      label: "LinkedIn",
      url: "https://www.linkedin.com/in/edwin-zamudio",
      iconClassName: "fa fa-linkedin"
    },
    {
      label: "Twitter",
      url: "https://twitter.com/ed_13",
      iconClassName: "fa fa-twitter"
    }
  ],
  copyright: "Copyright © 2019. Edwin Zamudio", // Copyright string for the footer of the website and RSS feed.
  themeColor: "#c62828", // Used for setting manifest and progress theme colors.
  backgroundColor: "#CCD9DB" // Used for setting manifest background color.
};

// Validate

// Make sure pathPrefix is empty if not needed
if (config.pathPrefix === "/") {
  config.pathPrefix = "";
} else {
  // Make sure pathPrefix only contains the first forward slash
  config.pathPrefix = `/${config.pathPrefix.replace(/^\/|\/$/g, "")}`;
}

// Make sure siteUrl doesn't have an ending forward slash
if (config.siteUrl.substr(-1) === "/")
  config.siteUrl = config.siteUrl.slice(0, -1);

// Make sure siteRss has a starting forward slash
if (config.siteRss && config.siteRss[0] !== "/")
  config.siteRss = `/${config.siteRss}`;

module.exports = config;
